html {
  margin: 0;
  padding: 0;
}
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
  overflow-y: auto;
  background: rgba(246, 126, 126, 1);
  /* background-color: white; */
}
h1,
p {
  display: block;
}
h1 {
  font-family: "Eater";
  font-size: 2.5rem;
  line-height: 1;
}
h3{
  font-family: "Eater";
}
.blocks {
  position: relative;
  width: 100vw;
  height: 100vh;
}
.block,
.desc {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  flex-direction: column;
  overflow: hidden;
  /* animation: opac .5s 1; */
  color: rgba(246, 126, 126, 0.8);
}
.front {
  width: 100% !important;
  height: 100% !important;
  position: absolute;
  z-index: 1000000000;
  cursor: pointer;
}
.content {
  z-index: 1000000000;
  font-weight: bold;
  color: #000;
  width: 80%;
  margin: 0 auto 1em;
  text-align: center;
}
.block > * {
  color: rgba(246, 126, 126, 0.8);
}
.box {
  height: 50vh;
  width: 50vw;
  position: absolute;
  transition: all 0.4s linear;
  animation: size 1s 1;
}
@media only screen and (max-width: 768px) {
  .box {
    min-height: 20vh;
    height: 30vh;
  width:100%;
  position: relative;
  overflow: hidden;
  }
}
.box:nth-child(1) {
  top: 0;
  left: 0;
}

.box:nth-child(2) {
  top: 0;
  right: 0;
}
.box:nth-child(3) {
  bottom: 0;
  left: 0;
}

.box:nth-child(4) {
  bottom: 0;
  right: 0;
}
.box:after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background: #000;
  top: 0;
  left: 0;
  transition: 0.3s ease;
  z-index: 100;
}

.box:hover:after {
  background: rgba(0, 0, 0, 0.9);
}
.desc {
  height: 100%;
  width: 100%;
  background: rgba(246, 126, 126, 0.5);
  position: absolute;
  top: 0;
  z-index: 1000000 !important;
  animation: scale 0.5s 1;
}
.visible {
  display: block;
}
.hide {
  display: none;
}
.close {
  top: 40px;
  right: 40px;
  font-size: 2rem;
  position: absolute;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}
.btn {
  display: inline-block;
  padding: 1rem 2rem;
  background: #000;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  margin: 1em auto;
  transition: all .3s ease;
  border:1px solid white;
}
.btn:hover{
  border-color: black;
  background-color: white;
  color: black;
}

@keyframes scale {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes size {
  0% {
    height: 25vh;
    width: 25vw;
  }
  100% {
    height: 50vh;
    width: 50vw;
  }
}
.container{
  max-width: 50rem;
  width: 90%;
  margin: 0 auto;
}
.team{
  display: flex;
  justify-content: space-between;
  margin:2rem auto;
  flex-direction: row;
  align-items: center;
  /* border:2px double black; */
  padding:1.5rem 2rem;
  background-color: white;
  border-bottom:0;
  /* background: rgba(246, 126, 126, 1); */
  -webkit-box-shadow: 0px 0px 24px 3px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 0px 24px 3px rgba(0,0,0,0.75);
box-shadow: 0px 0px 24px 3px rgba(0,0,0,0.75);
}
.team:last-child(){
  /* border-bottom:2px double black; */
}
.reverse{
  flex-direction: row-reverse;
}
.image-section img{
  max-width: 15rem;
  width: 100%;
  position: relative;
  display: block;
  transform: scale(1.1);
  transition: .4s ease-in-out;
}
.image-section:after{
  position: absolute;
  /* content:''; */
  height:100%;
  width: 100%;
  background-color: rgba(0,0,0, 0.6);
  top:0;
  transition: all .3s ease;
  display: none;
  z-index: 2;
}

.w-65{
  width: 65%;
}
.w-30{
  width: 30%;
}
.w-90{
  width: 90% !important;
  margin: 0 auto;
  display: block !important;
}
.image-section{
  position: relative;
  width: 30%;
}
.image-section:hover > .social-links{
  display: block;
}
.image-section:hover:after{
  display: block;
}
.image-section:hover img{
  transform: scale(0.8) rotate(5deg);
}

.social-links{
  position: absolute;
  background: rgba(246, 126, 126, 0.2);
  padding:.5rem;
  color:white;
  transition:all .3s linear;
  display:none;
  z-index: 10;
}

.social-links.one{
  top:0;
  left:0;
  border-bottom-right-radius: 50%;
}
.social-links.two{
  top:0;
  right:0;
  border-bottom-left-radius: 50%;
}
.social-links.three{
  bottom:0;
  left:0;
  border-top-right-radius: 50%;
}
.social-links.four{
  bottom:0;
  right:0;
  border-top-left-radius: 50%;
}.social-links:hover{
  transform:scale(1.2);
}
.header-title{
  font-size: 12px;
  display: inline;
  padding-left: .5rem;
  font-style: italic;
  font-weight: normal;
  vertical-align: middle;
  animation: title 3s 2;
  color: rgba(246, 126, 126, 1);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
@keyframes title{
  0%{
    opacity:0;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
.slick-slide{
  /* width: 100% !important; */
}
.absolute-center{
  position: absolute;
  top:30%;
  left: 50%;
  bottom: 50%;
  right: 50%;
  transform: translate(-50%,-50%);
}
@keyframes image{
  0%{
    transform: none;
  }
  50%{
    
  }
  100%{
    transform: none;
  }
}
.text-center{
  text-align: center;
}

.sidenav{
  position: relative;
  background:white;
  color:black;
  flex-direction: column;
  transition:all .5s linear;
  width: 100%;
  text-align: center;
}
.sidenav__item{
  display:inline-block;
  padding:2em 3em;
  font-weight: bold;
  color:rgba(246, 126, 126, 0.8);
  text-align: center;
}
.sidenav__item:hover{
  text-decoration: underline;
  cursor: pointer;
}
.sidenav__item--home{
  height:30px;
  width:30px;
  position: absolute;
  background:rgba(246, 126, 126, 0.8);
  border-radius: 50%;
  top:50%;
  bottom:50%;
  transform: translateY(-50%);
  left:100px;;
}

.mt-20{
  margin-top: 8em;
}